body {
  background-color:white;
  padding: 0%;
}
h1 {
  color:black;
  font-family:sans-serif;
}

/* Top */
header {
  background-color:#B6FFFB;
  padding-top:1em;
  padding-left:1em;
  padding-bottom:1em;

  display:flex;
  flex-direction:column;
  align-items:center;
}
h1.heading {
  margin: 0%;
}

/* Main */
main {
  background-color:#E5FFFD;
  margin-top: 1em;
  padding-bottom: 1em;
  
  display:flex;
  flex-direction:column;
  align-items:center;
}

/* Cases */
div.case {
  width: 90%;
}
/* Case Button */
button.case-button {
  margin-top:1em;
  margin-bottom:0em;
  background-color:#9CD5FF;
  border: 0px solid white;
  width:100%;
}
h1.case-button {
  color:white;
  margin: 0em;
  padding: 0.5em;
}
/* Case Content */
div.case-content {
  background-color: #DCDCDC;
  padding-right: 1em;
  padding-left: 1em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
p.case-content {
  background-color: #242424;
  color:white;
  padding: 0.5em;
}